main {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home,
.home-intro {
  justify-content: center;
  display: flex;
}
.home {
  height: 80vh;
  align-items: center;
  flex-direction: row;
  gap: 80px;
}
.home-intro {
  place-items: center;
  flex-direction: column;
  width: 400px;
}
.home-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
}
.btn-group {
  display: flex;
  gap: 10px;
  padding-top: 20px;
}
.btn-message,
.btn-order {
  padding: 5px 20px;
  font-size: 14px;
  font-weight: 700;
}
.btn-order {
  border: 1px solid var(--yellow);
  background-color: var(--yellow);
  border-radius: 20px;
  color: #fff;
}

.btn-order a {
  color: #fff;
  text-decoration: none;
}

.btn-order a:hover {
  color: var(--yellow);
  text-decoration: none;
}

.btn-message,
.btn-order:hover {
  background-color: #fff;
  color: var(--yellow);
}
.btn-order:hover {
  cursor: pointer;
}
.btn-message {
  border: 1px solid #fff;
  border-radius: 20px;
}
.btn-message:hover {
  border: 1px solid #000;
  color: #000;
  cursor: pointer;
}
.home-img img {
  display: flex;
  width: 400px;
  border-radius: 20px;
  filter: drop-shadow(0 0 0.1rem black);
}

/* Responsive */
@media screen and (max-width: 960px) {
  
  .home {
    padding-top: 30px;
    height: 100%;
    flex-direction: column;
    gap: 50px;
  }
}

@media screen and (max-width: 512px) {
  .home-intro {
    place-items: center;
    flex-direction: column;
    width: 300px;
    text-align: center;
  }
  .home-img img {
    display: flex;
    width: 300px;
    border-radius: 20px;
    filter: drop-shadow(0 0 0.1rem black);
  }
}
