@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:ital,wght@0,100..900;1,100..900&family=Rubik+Scribble&family=Silkscreen:wght@400;700&family=Whisper&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
	--font-logo: "Whisper", cursive;
	--font-nav-list: "MuseoModerno", sans-serif;
	--yellow: #ffc526;
	--white: #e6e6e6
}

header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

body::-webkit-scrollbar-thumb {
  background-color: #ffc526;
  border-radius: 20px;
}

body::-webkit-scrollbar {
  width: 4px;
	background-color: transparent
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: transparent;
}

main {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home,
.home-intro {
  justify-content: center;
  display: flex;
}
.home {
  height: 80vh;
  align-items: center;
  flex-direction: row;
  grid-gap: 80px;
  gap: 80px;
}
.home-intro {
  place-items: center;
  flex-direction: column;
  width: 400px;
}
.home-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
}
.btn-group {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  padding-top: 20px;
}
.btn-message,
.btn-order {
  padding: 5px 20px;
  font-size: 14px;
  font-weight: 700;
}
.btn-order {
  border: 1px solid var(--yellow);
  background-color: var(--yellow);
  border-radius: 20px;
  color: #fff;
}

.btn-order a {
  color: #fff;
  text-decoration: none;
}

.btn-order a:hover {
  color: var(--yellow);
  text-decoration: none;
}

.btn-message,
.btn-order:hover {
  background-color: #fff;
  color: var(--yellow);
}
.btn-order:hover {
  cursor: pointer;
}
.btn-message {
  border: 1px solid #fff;
  border-radius: 20px;
}
.btn-message:hover {
  border: 1px solid #000;
  color: #000;
  cursor: pointer;
}
.home-img img {
  display: flex;
  width: 400px;
  border-radius: 20px;
  filter: drop-shadow(0 0 0.1rem black);
}

/* Responsive */
@media screen and (max-width: 960px) {
  
  .home {
    padding-top: 30px;
    height: 100%;
    flex-direction: column;
    grid-gap: 50px;
    gap: 50px;
  }
}

@media screen and (max-width: 512px) {
  .home-intro {
    place-items: center;
    flex-direction: column;
    width: 300px;
    text-align: center;
  }
  .home-img img {
    display: flex;
    width: 300px;
    border-radius: 20px;
    filter: drop-shadow(0 0 0.1rem black);
  }
}

#root {
	--font-logo: "Whisper", cursive;
	--font-nav-list: "MuseoModerno", sans-serif;
	--yellow: #ffc526;
	--white: #e6e6e6
}

.navbar-nav {
	display: flex;
	justify-content: space-around;
	align-items: center;
	border-radius: 20px;
	width: 80%;
	-webkit-border-radius: 20px;
	-moz-border-radius: 20px;
	-ms-border-radius: 20px;
	-o-border-radius: 20px;
}

.navbar-nav .nav-list {
	list-style: none;
	display: flex;
	grid-gap: 30px;
	gap: 30px;
	justify-content: right;
	z-index: 999;
}

.navbar-nav .nav-list a {
	text-decoration: none;
	font-family: var(--font-nav-list);
	font-weight: 700;
	color: #000;
}

.navbar-nav .nav-list a:hover {
	color: var(--yellow)
}

.logo {
	font-family: var(--font-logo)
}

.hamburger-menu {
	display: none
}

@media screen and (max-width:960px) {
	.nav-list {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 10px 15px;
		border-radius: 10px;
		-webkit-backdrop-filter: blur(5px);
		        backdrop-filter: blur(5px);
		flex-direction: column;
		top: 10%;
		right: 15%;
		position: absolute;
		border: 1px solid black;
		transition: all ease-in-out .3s;
	}

	.hidden {
		opacity: 0;
		justify-content: center;
		align-items: center;
		padding: 10px 15px;
		list-style: none;
		border-radius: 10px;
		-webkit-backdrop-filter: blur(5px);
		        backdrop-filter: blur(5px);
		flex-direction: column;
		top: 10%;
		right: 15%;
		position: absolute;
		border: 1px solid black;
	}

	.hidden a {
		text-decoration: none;
		padding: 10px 15px;
		font-family: var(--font-nav-list);
		font-weight: 700;
		color: #000;
		transition: .3s;
		-webkit-transition: .3s;
		-moz-transition: .3s;
		-ms-transition: .3s;
		-o-transition: .3s
	}

	.nav-list li a {
		color: white;
	}


	.hamburger-menu {
		display: flex
	}
}

.hamburger-active {
	transform: rotate(90deg);
	width: 30px;
	height: 20px;
	position: relative;
	cursor: pointer;
	transition: all ease-in-out .3s;
}

.hamburger-menu {
	transition: all ease-in-out .3s;
	width: 30px;
	height: 20px;
	position: relative;
	cursor: pointer
}

.line {
	width: 100%;
	height: 2px;
	background-color: #000;
	position: absolute;
	transition: transform .3s
}

.line:first-child {
	top: 0
}

.line:nth-child(2) {
	top: 50%;
	transform: translateY(-50%)
}

.open .line:first-child {
	transform: translateY(8px) rotate(45deg)
}

.open .line:nth-child(2) {
	opacity: 0
}

.open .line:nth-child(3) {
	transform: translateY(-8px) rotate(-45deg)
}
